@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin font-group($size, $height) {
  font-family: "Montserrat", "Trebuchet MS", Helvetica, sans-serif;
  font-style: normal;
  font-size: $size;
  line-height: $height;
  font-weight: 400;
}

@mixin font-group-medium($size, $height) {
  font-family: "Montserrat Medium", "Trebuchet MS", Helvetica, sans-serif;
  font-style: normal;
  font-size: $size;
  line-height: $height;
  font-weight: 500;
}

@mixin font-group-semibold($size, $height) {
  font-family: "Montserrat Semibold", "Trebuchet MS", Helvetica, sans-serif;
  font-style: normal;
  font-size: $size;
  line-height: $height;
  font-weight: 600;
}

@mixin font-group-bold($size, $height) {
  font-family: "Montserrat Bold", "Trebuchet MS", Helvetica, sans-serif;
  font-style: normal;
  font-size: $size;
  line-height: $height;
  font-weight: 700;
}

@mixin font-group-small($size, $height) {
  font-size: $size;
  line-height: $height;
}

// Desktop first

@mixin vp-1159 {
  @media (max-width: $vp-1160 - 1) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: $vp-1024 - 1) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

@mixin vp-479 {
  @media (max-width: $vp-480 - 1) {
    @content;
  }
}

@mixin vp-415 {
  @media (max-width: $vp-416 - 1) {
    @content;
  }
}

